import service from '../../service.js'
export default{
	data(){
		return{
			verification:'获取验证码',
			disabled:false,
			account:'',
			password:'',
			repassword:'',
			imobile:'',
			verifycode:'',
			
		}
	},
	methods:{
		obtain(){ //获取验证码
			var that = this
			
			uni.request({
				url:service.api_root.reg.verifycode,//service.api_root.reg.verifycode,  //'/routine/login/verifycode',
				method:'get',
				data:{
					account:that.account,
					temp:'sms_reg'
				},
				success(res) {
					console.log(res)
					uni.showToast({
						icon:'none',
						title:res.data.msg
					})
					if(res.data.code == 200){
						that.verification = '60s'
						that.disabled = true
						that.timer = setInterval(function(){
							let num = that.verification.split('s')[0]
							num --
							that.verification = num+'s'
						},1000)
					}
					
				}
			})
		},
		register(){ //注册
			let that = this
			let data = {
				account:that.account,
				password:that.password,
				repassword:that.repassword,
				imobile:that.imobile,
				verifycode:that.verifycode
			}
			uni.request({
				url:service.api_root.reg.reg,
				method:'post',
				data:data,
				success(res) {
					uni.showToast({
						icon:'none',
						title:res.data.msg
					})
					if(res.data.code == 200){
						uni.redirectTo({
							url:'../logo/logo'
						})
					}
				}
			})
		}
	},
	watch:{
		verification(curval,oldval){// 监听定时器的num值
			if(curval == '-1s'){
				clearInterval(this.timer)
				this.verification = '重新获取'
				this.disabled = false
			}
		}
	},
	
}
